.card-item__item {
    padding: 15px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 240px;
    background: rgba(255, 255, 255, 0.65);
    border-radius: 8px;
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
    cursor: pointer;
    color: #000;

    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card-item__item:hover {
    transform: translateY(-5px);
    box-shadow: 0 14px 18px rgba(0, 0, 0, 0.2);
}

.card-item__item__descr {
    padding-top: 14px;
    font-weight: 400;
    font-size: 14px;
}

.card-item__item__img {
    min-height: 130px;
}

.card-item__item__country {
    font-weight: 400;
    font-size: 14px;
    padding-top: 10px;
    padding-right: 14px;
    text-align: right;
    align-self: flex-end
}

.card-item__item__price {
    font-weight: 400;
    font-size: 14px;
    padding-top: 10px;
    padding-right: 14px;
    text-align: right;
    align-self: flex-end
}

